<!-- 货品管理 -->
<template>
	<view>
		<LoadMoreList searchField="goodsName" apiUrl="/goods/getList" height="calc(100vh - 126rpx)"
			placeholder="输入货品名称搜索">
			<template #default="{ item }">
				<u-cell center @click="click(item)">
					<view slot="title" class="u-slot-title">
						<text class="name" style="font-size: 35rpx;">{{ item.goodsName }}</text>
						<view style="margin-top: 12rpx;">
							<u--text type="info" :text="item.cityName"></u--text>
						</view>
					</view>
					<view slot="value" class="u-slot-value">
						<u--text type="info" :text="item.goodsNumber"></u--text>
						<view style="margin-top: 12rpx;">
							<u--text :text="item.goodsNature == 0 ? '国产': '进口'"></u--text>
						</view>
					</view>
				</u-cell>
			</template>
		</LoadMoreList>
	</view>
</template>

<script>
	import LoadMoreList from '@/pages_sub/components/LoadMoreList.vue';

	export default {
		components: {
			LoadMoreList
		},
		data() {
			return {

			};
		},
		methods: {
			click(item) {
				uni.navigateTo({
					url: `/pages_sub/goods/details/details?params=${JSON.stringify(item)}`
				});
			}
		}
	}
</script>

